import mpvueCityPicker from '@/components/mpvue-citypicker/mpvueCityPicker.vue';
//来自 graceUI 的表单验证， 使用说明见手册 http://grace.hcoder.net/doc/info/73-3.html
import graceChecker from '@/common/plugs/graceChecker.js';
export default {
	components: {
		mpvueCityPicker
	},
	data() {
		return {
			model: {
				id: 0,
				consignee: '',
				mobile: '',
				province: '',
				city: '',
				area: '',
				address: '',
				is_default: 0,
				sort: 0,
				sex: 1
			},
			cuText: "",
			cityPickerValueDefault: [0, 0, 1],
			themeColor: '#007AFF',
			address: '请选择省市区',
			edit: false,
			id: '',
			addressList:[]
		};
	},
	onLoad(options) {
		this.init()
		if (options.id) {
			this.id = options.id;
			this.cuText = "编辑地址"
			this.api.center.address.info({
					id: options.id
				},
				res => {
					console.log(res);
					this.model = res.data;
					this.address = res.data.province + '-' + res.data.city + '-' + res.data.area;
				},
				err => {}
			);
			this.edit = true;
		} else {
			this.cuText = "创建地址"
		}
	},
	methods: {
		init(){
			// 获取到后台的省市区数据
			// this.api.center.address.get_region(null, res => {
			// 	console.log(res.data)
			// 	let arr1=[],cityData=[],area=[]
			// 	arr1 = res.data.map((el) => {
			// 		return {
			// 			"label": el.name,
			// 			"value": el.id
			// 		}
			// 	})
			// 	// console.log(JSON.stringify(arr1))
			// 	cityData = res.data.map((el) => {
			// 		return el.city.map((item) => {
			// 			return {
			// 				"label": item.name,
			// 				"value": item.id
			// 			}
			// 		})
			// 	})
			// 	area=res.data.map((el) => {
			// 		return el.city.map((item) => {
			// 			return item.district.map((sub)=>{
			// 				return {
			// 					"label": sub.name,
			// 					"value": sub.id
			// 				}
			// 			})
			// 		}) 
			// 	})
			// 	// console.log(JSON.stringify(area))
			// })
		},
		sexChange(e) {
			this.model.sex = e.detail.value;
		},
		switchChange(e) {
			if (e.detail.value) {
				this.model.is_default = 1;
			} else {
				this.model.is_default = 0;
			}
		},
		// 三级联动选择
		showMulLinkageThreePicker() {
			this.$refs.mpvueCityPicker.show();
		},
		onConfirm(e) {
			console.log(e);
			this.address = e.label;
			this.model.province = e.label.split('-')[0];
			this.model.city = e.label.split('-')[1];
			this.model.area = e.label.split('-')[2];
			console.log(this.model);
		},
		onCancel(){
			
		},
		async formSubmit(e) {
			var rule = [{
					name: 'consignee',
					checkType: 'string',
					checkRule: '1,10',
					errorMsg: '称呼应为1-10个字符'
				},
				{
					name: 'sex',
					checkType: 'in',
					checkRule: '1,2',
					errorMsg: '请选择性别'
				},
				{
					name: 'mobile',
					checkType: 'phoneno',
					checkRule: '',
					errorMsg: '请输入正确的手机号码'
				},
				{
					name: 'province',
					checkType: 'notnull',
					checkRule: '',
					errorMsg: '请选择地址'
				},
				{
					name: 'address',
					checkType: 'string',
					checkRule: '1,20',
					errorMsg: '详细地址应为1-20个字符'
				}
			];
			//进行表单检查
			var formData = e.detail.value;
			var checkRes = graceChecker.check(formData, rule);
			console.log(this.model);
			if (checkRes) {
				if (this.edit) {
					this.model.id = this.id;
				}
				this.api.center.address.handle(
					this.model,
					res => {
						console.log(res);
						uni.showToast({
							title: res.message,
							icon: 'success',
							success: () => {
								setTimeout(() => {
									uni.navigateBack({
										delta: 1
									});
								}, 1000);
							}
						});
					},
					err => {}
				);
			} else {
				uni.showToast({
					title: graceChecker.error,
					icon: 'none'
				});
			}
		}
	}
};
